}
.btn-add {
  margin-left: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px 18px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  letter-spacing: 2px;
  border: none;
  font-size: 12px;
  background: #5d5a6d;
}
.btn-add::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #1b2533;
  z-index: -1;
  transition: transform 0.5s;
  transform-origin: bottom right;
  transform: scale(0);
}
.btn-add:hover::before {
  transition: transform 0.5s;
  transform-origin: top left;
  transform: scale(1);
}
.btn-add::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  border: 2px solid #fff;
  box-sizing: border-box;
  z-index: -1;
  transition: transform 0.5s;
  transform-origin: top left;
  transform: scale(1);
}
.btn-add:hover::after {
  transition: transform 0.5s;
  transform-origin: bottom right;
  transform: scale(0);
}

.form-section {
    background: white;
    max-width: 550px;
    margin: 3vh auto 0;
    padding: 0 20px;
    max-height: 75vh;
  }

  .form-section .round-image {
    height: 250px;
    width: 250px;
    border-radius: 100%;
    overflow: hidden;
    margin: 0 auto 20px;
  }
  
  .form-section .round-image img {
    object-fit: cover;
    height: 100%;
    object-position: center;
  }
  
  .form-section .header {
    text-align: center;
  }
  
  .form {
    background: white;
    padding: 30px;
    border-radius: 20px;
    box-shadow: rgba(16, 35, 82, 0.02) 0 7px 50px 20px;
  }
  
  .form .title {
    margin: 10px 0;
  }
  
  .form-group .input {
    margin: 8px 0 12px;
    width: 100%;
    border-radius: 4px;
    display: block;
    border: none;
    font-size: 14px;
    background: #eff1f6;
    padding: 12px 12px;
  }
  
  .form-group .input-error {
    padding: 8px 16px;
    border-radius: 4px;
    background-color: rgb(255, 192, 192);
    color: rgb(255, 34, 34);
  }
  
  .form-group .text-area {
    display: block;
    font-size: 14px;
    width: 100%;
    background: #eff1f6;
    border: none;
    border-radius: 4px;
    line-height: 1.3;
    resize: none;
    box-sizing: border-box;
    padding: 12px;
    margin-top: 10px;
    margin-bottom: 10px;
    font-family: "Manrope", sans-serif;
  }
  
  .form-group .label {
    font-weight: 600;
  }
  
  .form button {
    margin-top: 30px;
    background: #5663ff;
    width: 100%;
    color: white;
    padding: 16px 0;
    font-size: 16px;
    border-radius: 10px;
    border: none;
  }
  
  .form-section.link {
    text-align: center;
  }
  
.App {
